.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #14240b;
  padding: 20px;
}

h1 {
  font-size: 2em;
  color: #333333;
  margin-bottom: 20px;
}

.menu-button {
  background-color: #1e4f5e;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
  margin-top: 20px;
}

.menu-button:hover {
  background-color: #1e4f5e;
}

.talent button {
  background-color: #041e25;

}

.talent {
  background-color: #1e4f5e;
}

